* {
  margin: 0;
  padding: 0;
  /* box-sizing: border-box; */
}

.container {
  background: linear-gradient(to right bottom, #5dd3b1, #e7813d);
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.app-wrapper {
  background: linear-gradient(to right bottom, #224641, #22544a);

  min-width: 450px;
  min-height: 550px;
  padding: 30px;
  box-sizing: border-box;
  border-radius: 12px;
  box-shadow: 5px 8px 50px #000;
  margin-bottom: 10px;
}
.header h1 {
  color: #fff;
  text-align: center;
  margin: 30px 0;
}
.task-input {
  outline: none;
  width: 260px;
  padding: 15px;
  margin-right: 25px;
  font-size: 20px;
  color: #ccc;
  background-color: #0000;
  border: 1px solid #24afac;
  border-radius: 10px;
}
.button-add {
  width: 70px;
  padding: 15px 15px;
  font-size: 20px;
  border-radius: 10px;
  border: 0;
  background-color: #4acf8e;
  cursor: pointer;
}
.list-item {
  display: flex;
  margin: 20px 0;
  border: 1px solid #ccc;
  border-radius: 10px;
  padding: 10px;
  max-height: 42px;
}
.button-complete,
.button-delete,
.button-edit {
  border: none;
  outline: none;
  cursor: pointer;
  background-color: transparent;
  font-size: 26px;
  border-radius: 50%;
}
.button-complete {
  color: #ff6c7d;
  margin-right: 10px;
}
.button-edit {
  color: #83dbe9;
  margin-right: 10px;
}
.button-delete {
  color: rgb(176, 222, 77);
}
.complete {
  text-decoration-style: solid;
  text-decoration-line: line-through;
  text-decoration-color: #c2cdc5;

  opacity: 0.6;
}
.list {
  width: 220px;
  background-color: transparent;
  border: none;
  color: #ccc;
  font-size: 20px;
  padding-left: 10px;
  margin-right: 15px;
}
input:focus,
button:focus {
  outline: none;
}

@media screen and (max-width: 795px) {
  .app-wrapper {
    min-width: 440px;
    min-height: 500px;
    padding: 25px;
    margin-bottom: 8px;
  }
  .header h1 {
    font-size: 26px;
    margin: 27px 0;
  }
  .task-input {
    width: 230px;
    padding: 13px;
    margin-right: 22px;
    font-size: 19px;
  }
  .button-add {
    width: 70px;
    padding: 13px 13px;
    font-size: 19px;
  }
  .list-item {
    display: flex;
    width: 85%;
    margin: 18px 0;
    padding: 8px;
    max-height: 39px;
  }

  .button-complete,
  .button-delete,
  .button-edit {
    border: none;
    outline: none;
    cursor: pointer;
    background-color: transparent;
    font-size: 22px;
    border-radius: 50%;
  }

  .list {
    width: 230px;
    font-size: 18px;
    padding-left: 8px;
    margin-right: 13px;
  }
}
@media screen and ((max-width: 531px)) {
  .app-wrapper {
    min-width: 100%;
    min-height: 610px;
    padding: 27px;
    margin-bottom: 12px;
  }
  .header h1 {
    font-size: 22px;
    margin: 22px 0;
  }
  .task-input {
    width: 230px;
    padding: 13px;
    margin-right: 20px;
    font-size: 16px;
  }
  .button-add {
    width: 70px;
    padding: 13px 13px;
    font-size: 19px;
  }
  .list-item {
    display: flex;
    width: 92%;
    margin: 20px 0;
    padding: 8px;
    max-height: 39px;
  }

  .button-complete,
  .button-delete,
  .button-edit {
    font-size: 17px;
  }

  .list {
    width: 230px;
    font-size: 17px;
    padding-left: 8px;
    margin-right: 13px;
  }
}
@media screen and ((max-width: 400px)) {
  .app-wrapper {
    padding: 24px;
  }
  .header h1 {
    font-size: 20px;
    margin: 20px 0;
  }
  .task-input {
    width: 200px;
    padding: 12px;
    margin-right: 19px;
    font-size: 14px;
  }
  .button-add {
    width: 60px;
    padding: 12px 12px;
    font-size: 16px;
  }
  .list-item {
    display: flex;
    width: 90%;
    margin: 20px 0;
    padding: 10px;
    max-height: 39px;
  }
  .list {
    width: 195px;
    font-size: 17px;
    padding-left: 8px;
    margin-right: 13px;
  }
  .button-complete,
  .button-delete,
  .button-edit {
    font-size: 19px;
    border-radius: 50%;
  }
}
